}

.form__small {
  height: 42px;
}

.form__input {
  box-sizing: border-box;
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: none;
  width: 100%;
  height: 100%;
  font-family: inherit;
  box-shadow: 0 1px 8px #3d4e611a;
}

