}

.form__label {
  width: 100%;
  display: block;
  position: relative;
}

.form__label:after {
  content: "";
  display: block;
  position: absolute;
}

.form__label-big:after {
  background-image: url("Search.ccd3c07b.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
